import {SUCCESS_LOGIN,FAILURE_LOGIN, LOGOUT} from '../Actions/LoginActions';

const initialState = JSON.parse(localStorage.getItem('authenticated'))?{"authenticated":JSON.parse(localStorage.getItem('authenticated')), "email":localStorage.getItem('user'), "loggedIn":true}:{loggedIn:false};

export function login(state=initialState, action){
    switch(action.type){
        case SUCCESS_LOGIN:
            return {
                ...state,
                loggedIn:true,
                authenticated:true,
                email:action.user.email
            };
        case FAILURE_LOGIN:
            return {};
        case LOGOUT:
            return {
                loggedIn:false
            };
        default:
            return state;
    }
}


